.showcase__container {
    display: flex;
    flex-wrap: wrap;
    text-align: center;
}

.showcase__container :hover {
    background-color: darkgray;
    color: white;
}

.prop__container {
    width: 30%;
    height: 5%;
    box-sizing: border-box;
    border: 4px solid #b4b4a6;
    border-radius: 20px;
    margin: 1%;
    text-decoration: none;
    color: #77776e;
}

.prop__container img {
    padding-top: 5px;
    width: 90%;
    object-fit: cover;
    object-position: center;
    border-radius: 20px;
}

.prop__container p {
    font-size: 15px;
}

.showcase__image {
    aspect-ratio: 1 / 1;
}