.header {
    top: 0;
    left: 0;
    width: 100%;
    height: 70px;
    position: fixed;
    background-color: #E9D7A4;
    border-bottom: 1px solid #E9D7A4;
}

.header__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.header__container__navbar ul {
    display: flex;
    align-items: center;
    list-style: none;
}
.header__container__logo {
    width: 150px;
}

.header__container__logo img {
    padding-left: 35px;
    width: 35px;
}

.header__container__navbar ul {
    padding: 0px;
}

.header__container__navbar ul li {
    margin: 6px 40px;
}

.header__container__navbar ul li a {
    font-size: 25px;
    text-decoration: none;
    color: #9B8F6C;
}

.header__container__navbar ul li a:hover {
    color: #5F5843;
}

.header__container__connexion {
    width: 150px;
}

.header__container__connexion ul {
    padding: 0px;
    list-style: none;
}

.header__container__connexion ul li a {
    font-size: 25px;
    text-decoration: none;
    color: #9B8F6C;
}

.header__container__connexion ul li a:hover {
    color: #5F5843;
}
