.container {
    padding-top: 70px;
    display: flex;
}

.registration__container, .connexion__container {
    padding-top: 5%;
    text-align: center;
    width: 50%;
}

.registration__container__email {
    padding-top: 5%;
}

.registration__container__password {
    padding-top: 5%;
}

.registration__input__email {
    width: 35%;
    height: 35px;
    font-size: 15px;
    border-radius: 20px;
    text-indent: 25px;
}

.registration__input__password {
    width: 35%;
    height: 35px;
    font-size: 15px;
    border-radius: 20px;
    text-indent: 25px;
}

.registration__container__button button {
    width: 15%;
    height: 35px;
    border: 1px solid gray;
    border-radius: 20px;
}

.registration__container__button button:hover {
    background-color: darkgray;
    color: white;
}








