.showcase {
    padding-top: 70px;
}

.showcase__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.showcase__top button {
    width: 15%;
    height: 35px;
    border: 1px solid gray;
    border-radius: 20px;
}

.showcase__top button:hover {
    background-color: darkgray;
    color: white;
}
